.socials {
  display: flex;
  @media (min-width: $desktops) {
    width: 330px;
    flex-direction: column;

    margin: 20px auto;
  }
  @media (min-width: $desktops-2) {
    margin: 16px auto 0;
  }
  @media (min-width: $desktops-4) {
    width: 478px;

    margin: 20px auto 0;
  }

  p {
    @media (min-width: $desktops) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 15px;
      letter-spacing: 0px;
      color: $color-default-white;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 22px;
    }
  }
  @media (max-width: 600px) {
    flex-direction: column;
  }
}

.socials__share {
  text-align: center;
}

.socials__inner {
  @media (min-width: $desktops) {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
  }
}

.socials__list {
  display: flex;
  @media (min-width: $desktops) {
    width: 52%;
  }

  @media (max-width: 600px) {
    width: 100%;
    justify-content: center;
  }
}

.socials__item {
  @media (min-width: $desktops) {
    width: 50px;
    height: 50px;
    border-radius: 50%;

    background-color: $color-blue;
    margin-right: 8px;
    text-align: center;
    padding: 16px 0;

    &:last-of-type {
      margin-right: 0;
    }

    a {
      text-align: center;
      color: $color-default-white;
      font-size: 15px;
      font-style: normal;
      font-weight: 500;
      line-height: 17px;
      letter-spacing: 0px;
      text-align: center;
    }
  }

  @media (min-width: $desktops-2) {
    width: 50px;
    height: 50px;
    margin-right: 10px;
    text-align: center;
    padding: 12px 0;
  }
  @media (min-width: $desktops-4) {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: $color-blue;
    margin-right: 10%;
    text-align: center;
    padding: 16px 0;

    &:last-of-type {
      margin-right: 0;
    }

    a {
      font-size: 20px;
      line-height: 22px;
    }
  }
  @media (max-width: 600px) {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: $color-blue;
    margin-right: 5%;
    text-align: center;
    padding: 10px 0;

    &:last-of-type {
      margin-right: 0;
    }

    a {
      text-align: center;
      color: $color-default-white;
      font-size: 15px;
      font-style: normal;
      font-weight: 500;
      line-height: 17px;
      letter-spacing: 0px;
      text-align: center;
    }
  }
}
