.skyscraper {
  @media (min-width: $desktops) {
  }
  @media (min-width: $desktops-2) {
  }
  @media (min-width: $desktops-4) {
    // display: flex;
  }
  @media (max-width: 600px) {
    margin-top: 70px;
  }
}

.skyscraper__content {
  @media (min-width: $desktops) {
    display: flex;
    flex-direction: column;
    padding-top: 60px;
    margin: 0 auto;
    width: 413px;
  }
  @media (min-width: $desktops-2) {
    width: 461px;
    padding-top: 30px;
  }
  @media (min-width: $desktops-4) {
    padding-top: 75px;
    width: 690px;
  }

  p {
    @media (min-width: $desktops) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 17px;
      letter-spacing: 0px;
      text-align: center;
      color: $color-default-white;
    }
    @media (min-width: $desktops-2) {
    }
    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 24px;
    }

    @media (max-width: 600px) {
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 16px;
      letter-spacing: 0px;
      text-align: center;
      color: $color-default-white;
    }
  }
}

.skyscraper__picture {
  @media (min-width: $desktops) {
    width: 413px;
    height: 329px;
    position: relative;

    img {
      object-fit: fill;
      width: 100%;
      height: 100%;
    }

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/Img_Rhomb-1024-gold.png");
      width: 140px;
      height: 140px;
      top: -100px;
      right: -12%;
    }
  }
  @media (min-width: $desktops-2) {
    width: 461px;
    height: 368px;

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/Img_Rhomb-1366-gold.png");
      width: 160px;
      height: 160px;
      top: -100px;
      right: -12%;
    }
  }
  @media (min-width: $desktops-4) {
    width: 690px;
    height: 550px;

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/Img_Rhomb-04.png");
      width: 200px;
      height: 200px;
      top: -135px;
      right: -12%;
    }
  }
  @media (max-width: 600px) {
    width: 100%;
    height: auto;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}
