.next {
  @media (min-width: $desktops) {
    position: absolute;
    bottom: -40px;
    right: 0;
  }

  @media (min-width: $desktops-2) {
    bottom: 72px;
  }

  @media (min-width: $desktops-4) {
    bottom: 0;
  }

  @media (max-width: 600px) {
    margin-top: 70px;
    padding-bottom: 30px;
  }

  h3 {
    @media (max-width: 600px) {
      color: $color-default-white;
      font-size: 28px;
      font-style: normal;
      font-weight: 500;
      line-height: 29px;
      letter-spacing: 0px;
      text-align: right;

      &:before {
        display: none;
      }
    }
  }

  span {
    @media (max-width: 600px) {
      color: $color-default-white;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 15px;
      letter-spacing: 0px;
      text-align: right;

      margin-top: 15px;
      display: block;
    }
  }
}

.next__picture {
  @media (min-width: $desktops) {
    width: 260px;
    margin-left: auto;

    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: -55px;
      left: 0;
      background: transparent;
      background: linear-gradient(
        270deg,
        #98b169 0%,
        rgba(152, 177, 105, 0) 100%
      );
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  @media (min-width: $desktops-2) {
    width: 330px;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: transparent;
      background: linear-gradient(
        270deg,
        #98b169 0%,
        rgba(152, 177, 105, 0) 100%
      );
    }
  }

  @media (min-width: $desktops-4) {
    width: 445px;

    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: transparent;
      background: linear-gradient(
        270deg,
        #98b169 0%,
        rgba(152, 177, 105, 0) 100%
      );
    }
  }
  @media (max-width: 600px) {
    margin-top: 40px;
    width: 100%;
    height: auto;
    // background: linear-gradient(360deg, #98B169 9.21%, rgba(152, 177, 105, 0) 95.26%);
    background-image: linear-gradient(
        360deg,
        #98b169 9.21%,
        rgba(152, 177, 105, 0) 95.26%
      ),
      url("../img/kotelnicheskaya.png");
    background-repeat: no-repeat;
    min-height: 204px;
    background-position: center;
    position: relative;

    &:before {
      position: absolute;
      content: "";
      width: 160px;
      height: 160px;
      top: -40px;
      left: 0;
      background-image: url("../img/star_next.png");
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

.next__inner {
  position: relative;
}

.next__content {
  @media (min-width: $desktops) {
    h3 {
      font-size: 25px;
      font-style: normal;
      font-weight: 500;
      line-height: 26px;
      letter-spacing: 0px;
      text-align: right;

      margin: 10px 0 8px 0;
    }

    span {
      display: block;
      text-align: right;

      color: $color-default-white;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 15px;
      letter-spacing: 0px;
      width: fit-content;
      margin-left: auto;
      position: relative;

      &:after {
        position: absolute;
        content: "";
        width: 100%;
        background-color: $color-default-white;
        height: 1px;
        bottom: -5px;
        right: 0;
      }
    }
  }
  @media (min-width: $desktops-2) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-75%, -20%);
    h3 {
      margin: 10px 0 8px 0;
    }

    // span {
    //   &:after {
    //     position: absolute;
    //     content: "";
    //     width: 100%;
    //     background-color: $color-default-white;
    //     height: 1px;
    //     bottom: 0;
    //     right: 0;
    //   }
    // }
  }

  @media (min-width: $desktops-4) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-75%, -50%);
    h3 {
      font-size: 35px;
      font-style: normal;
      font-weight: 500;
      line-height: 37px;
      letter-spacing: 0px;
      text-align: right;

      margin: 10px 0 8px 0;
    }

    span {
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      letter-spacing: 0px;

      &:after {
        position: absolute;
        content: "";
        width: 100%;
        background-color: $color-default-white;
        height: 1px;
        bottom: 0;
        right: 0;
      }
    }
  }
  @media (max-width: 600px) {
    padding-right: 20%;
    padding-top: 38px;

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/ellipse.png");
      background-repeat: no-repeat;
      right: 0;
      top: 0;
      width: 240px;
      height: 320px;
      z-index: -1;
    }
  }
}

.next__texts {
  @media (min-width: $desktops) {
    position: absolute;
    transform: translateX(-50%);

    h3 {
      font-size: 25px;
      font-style: normal;
      font-weight: 500;
      line-height: 26px;
      letter-spacing: 0px;
      text-align: right;

      margin: 0 0 10px 0;
    }

    span {
      width: fit-content;
      margin-left: auto;
      display: block;

      position: relative;

      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 15px;
      letter-spacing: 0px;
      text-align: right;

      &:after {
        position: absolute;
        content: "";
        width: 100%;
        background-color: $color-default-white;
        height: 1px;
        bottom: -5px;
        right: 0;
      }
    }
  }
  @media (min-width: $desktops-2) {
    transform: translateX(-50%);
  }
  @media (min-width: $desktops-4) {
    h3 {
      font-size: 35px;
      font-style: normal;
      font-weight: 500;
      line-height: 37px;
      letter-spacing: 0px;
      text-align: right;
      margin: 0 0 10px 0;
    }
  }
}
