.neighbour {
  margin-top: 70px;
  @media (min-width: $desktops) {
    margin-top: 0;
    h3 {
      font-size: 50px;
      font-style: normal;
      font-weight: 400;
      line-height: 55px;
      letter-spacing: -1px;
      text-align: left;
      color: $color-default-white;
      text-indent: 40px;
      margin: 60px 0 20px;
      position: relative;

      width: 666px;

      &:before {
        position: absolute;
        content: "";
        width: 126px;
        height: 126px;

        background-image: url("../img/star_1024.png");
        top: -155px;
        left: 21%;
        background-repeat: no-repeat;
      }
    }
  }
  @media (min-width: $desktops-2) {
    h3 {
      font-size: 60px;
      font-weight: 400;
      line-height: 66px;
      margin: 47px 0 20px;
      text-indent: 50px;

      width: 790px;

      &:before {
        width: 144px;
        height: 144px;
        background-image: url("../img/star_1366.png");
        top: -150px;
        left: 27%;
      }
    }
  }
  @media (min-width: $desktops-4) {
    h3 {
      font-size: 80px;
      font-weight: 400;
      line-height: 88px;
      width: 1054px;

      margin: 65px 0 36px;

      &:before {
        width: 180px;
        height: 180px;
        background-image: url("../img/star.png");
        top: -175px;
        left: 33%;
      }
    }
  }
  h3 {
    @media (max-width: 600px) {
      font-size: 25px;
      font-style: normal;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: -1px;
      text-align: left;
      text-indent: 10px;
      margin: 0 0 10px 0;

      &:before {
        display: none;
      }
    }
  }

  @media (max-width: 600px) {
    margin-top: 50px;
  }
}

.neighbour__subtitle {
  transform: rotate(-90deg);
  color: $color-default-white;
  margin: 50px 0 0 0;
  position: relative;

  @media (min-width: $desktops) {
    writing-mode: vertical-rl;
    transform: rotate(-180deg);
    color: $color-default-white;

    margin: 35px 0 0px 8px;

    font-size: 35px;
    font-style: normal;
    font-weight: 400;
    line-height: 35px;
    letter-spacing: 0em;
    text-align: right;

    &:after {
      position: absolute;
      content: "";
      height: 100%;
      width: 1px;
      background-color: $color-default-white;
      bottom: 0;
      left: -14px;
    }

    &:before {
      position: absolute;
      content: "";
      height: 100px;
      background-image: url("../img/img_Rhomb-1024-silver.png");
      width: 100px;
      top: -25px;
      background-repeat: no-repeat;
      right: -40%;
    }
  }

  @media (min-width: $desktops-2) {
    margin: 28px 0 0px -8px;

    &:after {
      position: absolute;
      content: "";
      height: 100%;
      width: 1px;
      background-color: $color-default-white;
      bottom: 0;
      left: -5px;
    }

    &:before {
      position: absolute;
      content: "";
      height: 114px;
      background-image: url("../img/img_Rhomb-1366-silver.png");
      width: 114px;
      top: 48px;
      right: -50%;
    }
  }

  @media (min-width: $desktops-4) {
    margin: 40px 0 0 0;

    font-size: 40px;
    font-weight: 400;
    line-height: 40px;

    letter-spacing: 0em;
    text-align: right;
    z-index: 2;

    &:after {
      position: absolute;
      content: "";
      height: 100%;
      width: 1px;
      background-color: $color-default-white;
      bottom: 0;
      left: -5px;
    }

    &:before {
      position: absolute;
      content: "";
      height: 140px;
      background-image: url("../img/star_rhomb.png");
      width: 140px;
      top: 80px;
      right: -50%;
    }
  }

  @media (max-width: 600px) {
    display: none;
  }
}

.neighbour__text {
  @media (min-width: $desktops) {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
  }
  @media (min-width: $desktops-2) {
    padding-right: 170px;
  }
  @media (min-width: $desktops-4) {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;

    padding-right: 150px;
  }
  @media (max-width: 600px) {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
  }
}

.neighbour__picture {
  position: relative;
  flex-shrink: 0;

  @media (min-width: $desktops) {
    height: 318px;
    width: 508px;

    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/rozetka_1024.png");
      width: 148px;
      height: 148px;
      right: 6%;
      top: -125px;
      z-index: -1;
      background-repeat: no-repeat;
    }

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/red-star_1366.svg");
      width: 73px;
      height: 73px;
      right: 21.5%;
      top: -165px;
      z-index: -1;
      background-repeat: no-repeat;
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  @media (min-width: $desktops-2) {
    height: 357px;
    width: 570px;

    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/rozetka_1366.png");
      width: 168px;
      height: 168px;
      right: -14%;
      top: -112px;
      z-index: -1;
    }

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/red-star_1366.svg");
      width: 73px;
      height: 73px;
      right: 3.5%;
      top: -150px;
      z-index: -1;
    }
  }

  @media (min-width: $desktops-4) {
    height: 541px;
    width: 863px;

    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/Rozetka.png");
      width: 267px;
      height: 267px;
      right: -4%;
      top: -180px;
      z-index: -1;
    }

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/red-star.svg");
      width: 104px;
      height: 104px;
      right: 15.5%;
      top: -235px;
      z-index: -1;
    }
  }

  @media (max-width: 600px) {
    margin-top: 40px;
    width: 100%;
    height: auto;

    &:before {
      @media (max-width: 600px) {
        position: absolute;
        content: "";
        background-image: url("../img/ornament.png");
        width: 140px;
        height: 195px;
        right: 0;
        top: -35px;
        z-index: -1;
      }
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

.neighbour__additional {
  @media (max-width: 600px) {
    display: flex;
    align-items: flex-end;
    margin-top: -45px;
    p {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;
      color: $color-default-white;
      margin: 0;
      padding: 0 0 10px 14px;
    }
  }
}

.neighbour__main-info {
  display: flex;

  @media (min-width: $desktops) {
  }
  @media (min-width: $desktops-2) {
  }
  @media (min-width: $desktops-4) {
  }
}

.neighbour__inner {
  @media (min-width: $desktops) {
    display: flex;
  }
}

.neighbour__content {
  @media (min-width: $desktops) {
    padding-left: 4%;
  }
  @media (min-width: $desktops-2) {
    padding-left: 3.5%;
  }
  @media (min-width: $desktops-4) {
    padding-left: 3%;
  }
}

.neighbour__texts {
  @media (min-width: $desktops) {
    width: 36%;
    margin-right: 7%;
  }
  @media (min-width: $desktops-2) {
    width: 41%;
    margin-right: 3%;
  }
  @media (min-width: $desktops-4) {
    width: 36%;
    margin-right: 5%;
  }
}

.neighbour__picture-wrapper {
  @media (min-width: $desktops) {
    width: 60%;
    transform: translateY(25px);
    span {
      font-size: 12px;
      line-height: 14px;
    }

    span {
      font-size: 12px;
      font-style: italic;
      font-weight: 400;
      line-height: 14px;
      letter-spacing: 0px;
      text-align: left;
      color: $color-default-white;

      margin-bottom: 10px;
      display: block;
    }
  }
  @media (min-width: $desktops-2) {
    width: 52.5%;
    transform: translateY(-15px);
    span {
      font-size: 12px;
      line-height: 14px;
    }
  }
  @media (min-width: $desktops-4) {
    width: 59%;
    transform: translateY(-25px);
    span {
      font-size: 16px;
      font-weight: 400;
      line-height: 19px;
    }
  }
}

.neighbour__closer {
  position: relative;

  display: flex;
  align-items: center;
  transform: translateX(105px);

  width: 330px;

  margin-top: 70px;
  z-index: 1;

  @media (min-width: $desktops) {
    p {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 19px;
      letter-spacing: 0px;
      text-align: left;

      color: $color-default-white;

      width: 255px;
      text-indent: 30px;
    }
  }

  @media (min-width: $desktops-2) {
    width: 330px;
    display: flex;
    align-items: center;
    transform: translateX(240px);

    margin-top: 45px;
    z-index: 1;

    p {
      width: 255px;
      text-indent: 30px;
    }
  }

  @media (min-width: $desktops-4) {
    width: 500px;
    display: flex;
    align-items: center;
    transform: translateX(240px);

    margin-top: 105px;
    z-index: 1;

    p {
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: 27px;
      letter-spacing: 0px;
      text-align: left;
      color: $color-default-white;

      width: 255px;
      text-indent: 30px;
    }
  }
}

.neighbour-slide.swiper-slide-prev {
  transition: $transition-settings;
  @media (min-width: $desktops) {
    right: -15%;
  }

  @media (min-width: $desktops-2) {
    right: -25%;
  }

  @media (min-width: $desktops-4) {
    right: -25vw;
  }
}

.swiper-slide-active + .neighbour-slide {
  transition: $transition-settings;
  @media (min-width: $desktops) {
    left: 0;
  }

  @media (min-width: $desktops-2) {
    left: -28%;
  }

  @media (min-width: $desktops-4) {
    left: -15%;
  }

  @media (min-width: 1900px) {
    left: -25%;
  }
}
